.live-list {
  width: 100%;
  display: block;
  min-height: 100vh;
  background: #F6F7FB;
  min-width: 1366px;
}
.live-list .clearfix:after {
  content: '';
  display: block;
  height: 0;
  overflow: hidden;
  clear: both;
  zoom: 1;
}
.live-list .live-list-pic {
  display: block;
  width: 100%;
  padding: 60px 0;
}
.live-list .live-list-pic .text-back {
  vertical-align: middle;
  margin-left: 10px;
  font-size: 16px;
}
.live-list .live-list-pic .top {
  height: 60px;
  line-height: 60px;
  background: #FFFFFF;
  box-shadow: 0 4px 15px rgba(45, 64, 101, 0.08);
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  color: #2A283D;
  opacity: 0.99;
  position: fixed;
  text-align: center;
  width: 100%;
  display: block;
  z-index: 999;
  top: 0;
}
.live-list .live-list-pic .top .img {
  position: absolute;
  left: 56px;
  top: 50%;
  width: 20px;
  height: 22px;
  transform: translate(0, -50%);
}
.live-list .live-list-pic .top .img img {
  display: block;
}
.live-list .live-list-pic .top .text-back {
  line-height: 20px;
}
.live-list .live-list-pic .top .title {
  display: inline-block;
  line-height: 60px;
  height: 60px;
}
.live-list .live-list-pic .list-warp {
  background: #fff;
  padding: 50px 30px 30px;
  margin: 0 30px;
}
.live-list .live-list-pic .list-warp .title {
  font-style: normal;
  font-weight: normal;
  font-size: 28px;
  line-height: 120.7%;
  color: #2A283D;
  display: block;
  margin-bottom: 16px;
}
.live-list .live-list-pic .select_top {
  width: 100%;
  height: 59px;
  line-height: 59px;
  border-bottom: solid 1px #eee;
  margin-bottom: 20px;
}
.live-list .live-list-pic .select_top .ant-input {
  color: #333;
}
.live-list .live-list-pic .select_top .ant-input::-webkit-input-placeholder {
  color: #888;
}
.live-list .live-list-pic .select_top .ant-input:-moz-placeholder {
  color: #888;
}
.live-list .live-list-pic .select_top .ant-input::-moz-placeholder {
  color: #888;
}
.live-list .live-list-pic .select_top .ant-input:-ms-input-placeholder {
  color: #888;
}
.live-list .live-list-pic .select_top .left {
  float: left;
  margin-left: 10px;
}
.live-list .live-list-pic .select_top .left .ant-btn-primary {
  background: #3E78ED !important;
}
.live-list .live-list-pic .select_top .left .ant-btn {
  border-radius: 2px !important;
  transition: none;
  width: 102px;
}
.live-list .live-list-pic .select_top .left .ant-btn-primary:hover {
  color: #fff !important;
}
.live-list .live-list-pic .select_top .left .ant-btn:hover {
  color: #3E78ED;
  border: 1px solid #3E78ED;
}
.live-list .live-list-pic .select_top .left .ant-col {
  width: auto;
}
.live-list .live-list-pic .select_top .left .ant-col .ant-btn {
  margin-right: 13px;
  height: 30px;
  font-size: 12px;
}
.live-list .live-list-pic .select_top .left div {
  display: inline-block;
}
.live-list .live-list-pic .select_top .right {
  float: right;
}
.live-list .live-list-pic .select_top .right .ant-input-search {
  width: 217px;
  height: 30px;
  margin-right: 22px;
}
.live-list .live-list-pic .select_top .right input {
  width: 100%;
  height: 100%;
  background: #F4F4F4;
  border-radius: 13.5px;
  outline: none;
  border: none;
  padding-left: 30px;
  font-size: 12px;
}
.live-list .live-list-pic .select_top .right .ant-input-suffix {
  width: 14px;
  height: 14px;
}
.live-list .live-list-pic .select_top .right .count {
  margin-right: 20px;
  font-size: 12px;
  color: #888;
  line-height: 27px;
}
.live-list .live-list-pic .clearfix:after {
  content: '';
  display: block;
  height: 0;
  overflow: hidden;
  clear: both;
  zoom: 1;
}
.live-list .live-list-pic .list-container {
  display: block!important;
  margin-bottom: 30px;
}
.live-list .live-list-pic .list-container .ant-col {
  width: 16.6%;
  padding: 0 8px;
}
.live-list .live-list-pic .list-container .rside-col {
  margin-bottom: 15px;
  padding: 0 8px;
}
.live-list .live-list-pic .list-container .rside-col dl {
  display: flex;
  background: #FAFAFA;
  border: 1px solid #EDEDED;
  box-sizing: border-box;
  border-radius: 6px;
  overflow: hidden;
  min-width: auto;
  position: relative;
  padding-left: 47px;
}
.live-list .live-list-pic .list-container .rside-col dt {
  position: absolute;
  left: 0;
}
.live-list .live-list-pic .list-container .rside-col dt img {
  width: 47px;
  height: 83px;
}
.live-list .live-list-pic .list-container .rside-col dd {
  padding: 12px 10px 0;
  box-sizing: border-box;
  width: 100%;
  display: block;
}
.live-list .live-list-pic .list-container .rside-col dd div {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  display: block;
  color: #000;
  padding-left: 5px;
  cursor: pointer;
}
.live-list .live-list-pic .list-container .rside-col dd p {
  margin: 0;
}
.live-list .live-list-pic .list-container .rside-col dd p span {
  margin: 0 5px;
  color: rgba(0, 0, 0, 0.3);
  font-size: 12px;
}
